@media only screen and (min-width: 1007px) {
    .nav-link:hover .tooltip-wrapper .tooltip-text {
        visibility: visible;
        /* display: inline; */
    }
}
.scroll-link {
    font-size: 1.5rem;
    cursor: pointer;
}

.scroll-link:hover {
    border-bottom: 2px solid var(--text-title);
}

.scroll-link > span {
    display: none;
}
#content-wrapper {
    margin: 3rem 0;
}

.nav-items > li,
.social-items > li,
.scroll-items > li {
    margin: 0.5rem;
    list-style: none;
}

.nav-text a,
.social-text a {
    /* margin: 0.5rem; */
    color: var(--text-primary);
    text-decoration: none;
    font-size: 1.5rem;
}

svg {
    color: var(--text-primary)!important;
}

