.social-text a:hover {
    /* background-color: var(--text-title); */
    border-bottom: 2px solid var(--text-title);
}

.nav-link > span,
.social-link > span {
    display: none;
}

/* VERTICAL */
.nav-container {
    position: fixed;
    top: -0.5rem;
    right: 3rem;
    transition: top 0.3s;
    z-index: 9000;
}
.social-container {
    position: fixed;
    bottom: -0.5rem;
    left: 2rem;
    transition: bottom 0.3s;
    z-index: 9000;
}
.scroll-container {
    position: fixed;
    bottom: -0.5rem;
    right: 3rem;
    transition: bottom 0.3s;
    z-index: 9000;
}

.nav-items,
.social-items,
.scroll-items {
    width: auto;
    height: auto;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    align-content: flex-end;
}

.vertical-line {
    margin-left: 0.6rem;
    height: 25px;
    width: 3px;
    background-color: var(--text-title);
    z-index: 9999;
}
.horizontal-line {
    display: none;
    margin-bottom: 0.6rem;
    height: 2px;
    width: 25px;
    background-color: var(--text-title);
    z-index: 9999;
}

/* MOBILE:640px TABLET:1007px */
@media only screen and (max-width: 1007px) {
    /* HORIZONTAL */
    .vertical-line {
        display: none;
    }
    .horizontal-line {
        display: block;
    }
    .nav-container {
        position: fixed;
        top: 0;
        left:0;
        right: -0.5rem;
    }
    .social-container {
        position: fixed;
        bottom: 0;
        left: -0.5rem;
        right: auto;
    }
    .scroll-container {
        position: fixed;
        bottom: 0;
        left: auto;
        right: -0.5rem;
    }
    .nav-items,
    .scroll-items {
        flex-direction: row;
        justify-content: flex-end;
        align-items: center;
        text-align: right;
    }
    .scroll-items {
        flex-direction: row-reverse;
    }
    .social-items {
        flex-direction: row;
        justify-content: flex-start;
        align-items: center;
        text-align: left;
    }
}
