.carousel-button .next-arrow,
.carousel-button .prev-arrow {
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    padding: 0.5rem;
    cursor: pointer;
    z-index: 9999;
}

.fade-enter {
    opacity: 0;
    transform: scale(0.96);
    z-index: 1;
}
.fade-enter-active {
    opacity: 1;
    transform: scale(1);
    transition: 250ms ease-in;
    transition-property: transform, opacity;
    z-index: 1;
}
.fade-exit {
    transform: scale(1);
    opacity: 1;
}
.fade-exit-active {
    opacity: 0;
    transform: scale(0.96);
    transition: 200ms ease-in;
    transition-property: transform, opacity;
}
.project-wrapper {
