}

.contact-header {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.contact-header > div {
    flex-shrink: 0;
    flex-basis: 49%;
}

/* SMALL: MOBILE */
@media only screen and (max-width: 640px) {
    .contact-header {
        flex-direction: column;
    }
}

